import React from "react"
import { Link } from "gatsby"

const PostList = ({ location, posts, withAuthor, withoutDate, prevTitle }) => {
  return (
    <ol className="no-style">
      {posts.map(tab => {
        const title = tab.frontmatter.title || tab.fields.slug

        return (
          <li key={tab.fields.slug} className="post-list-item">
            <div className="meta">
              {!withoutDate && (
                <time dateTime={tab.frontmatter.date}>
                  {tab.frontmatter.date}
                </time>
              )}
              {withAuthor && <span>{tab.frontmatter.author}</span>}
            </div>
            <Link
              to={tab.fields.slug}
              itemProp="url"
              state={{ prevPath: location.pathname, prevTitle }}
            >
              <span>{title}</span>
            </Link>{" "}
          </li>
        )
      })}
    </ol>
  )
}

export default PostList
