import React from "react"
import { graphql } from "gatsby"

import Layout from "../../components/layout"
import SEO from "../../components/seo"
import PostList from "../../components/post-list"
import BackButton from "../../components/back-button"

const Guitar = ({ data, location, path, prevTitle }) => {
  const title = "Guitarra"

  console.log(data)

  return (
    <Layout location={location} title={title}>
      <SEO title={title} />
      <h1>{title}</h1>
      <BackButton location={location} path={path} title={prevTitle} />
      <span className="sub-title">Tablaturas</span>
      <PostList
        posts={data.allMarkdownRemark.nodes}
        location={location}
        withAuthor={true}
        withoutDate={true}
        prevTitle={title}
      />
    </Layout>
  )
}

export default Guitar

export const guitarTabs = graphql`
  query {
    site {
      siteMetadata {
        title
      }
    }
    allMarkdownRemark(
      sort: { fields: [frontmatter___author, frontmatter___title], order: DESC }
      filter: {
        frontmatter: { draft: { ne: true } }
        fileAbsolutePath: { regex: "/guitar/" }
      }
      limit: 5
    ) {
      nodes {
        fields {
          slug
        }
        frontmatter {
          date(formatString: "YYYY-MM-DD")
          title
          author
        }
      }
    }
  }
`
